@import url(https://fonts.googleapis.com/css2?family=Inconsolata:wght@300;400;500&family=Libre+Barcode+39+Text&family=Open+Sans:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    box-sizing: border-box;
    font-family: 'Open Sans', sans-serif;
    font-size: 1rem;
    font-weight: 400;
    margin: 0;
    padding: 0;
    position: relative;
    color: rgba(255, 255, 255, 1);
    scroll-behavior: smooth;
}

:root {
    --grey-purp: rgb(84, 74, 77);
    --light-green: rgba(88, 128, 74, 0.3);
    scroll-behavior: smooth;
}

html, body {
    height: 100%;
    scroll-behavior: smooth;
}

main {
    overflow-y: scroll;
    position: fixed;
    top: 120px;
    bottom: 40px;
    -webkit-scroll-snap-type: y mandatory;
        -ms-scroll-snap-type: y mandatory;
            scroll-snap-type: y mandatory;
    width: 100%;
}

section {
    height: 100%;
    padding: 5%;
    scroll-snap-align: start;
}

#welcome {
    padding-top: 15%;
}

#contact {
    padding-top: 15%;
}

a {
    color: rgb(84, 74, 77);
    color: var(--grey-purp);
    text-decoration: none;
}

a:visited {
    color: rgb(84, 74, 77);
    color: var(--grey-purp);
}

a:hover {
    color: rgb(255, 255, 255);
}

h2 {
    background-color: transparent;
    font-family: 'Inconsolata', monospace;
    font-size: 1.4rem;
    font-weight: 400;
    margin: 0 auto;
    padding: 20px 8px 5px 8px;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
}

p {
    color: rgb(84, 74, 77);
    color: var(--grey-purp);
    font-family: 'Inconsolata', monospace;
    font-size: 1.3rem;
    padding: 5%;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
}

.emoji {
    background-color: rgba(88, 128, 74, 0.3);
    background-color: var(--light-green);
    color: white;
    font-size: 1.5rem;
}


/* PORTFOLIO */

#portfolio {
    overflow: hidden;
}

.portfolio-container {
    bottom: calc((100vh - 495px)/2);
    height: 600px;
    left: calc((100vw - 305px)/2);
    margin: 0 auto;
    overflow-y: scroll;
    /* padding-bottom: 60px;
    padding-left: 30px; */
    position: absolute;
    scroll-behavior: smooth;
    width: -webkit-min-content;
    width: -moz-min-content;
    width: min-content;
}

.card-wrapper {
    bottom: 0;
    height: 275px;
    position: -webkit-sticky;
    position: sticky;
    width: 275px;
}

#card-wrapper0 {
    z-index: 6;
}

#card-wrapper1 {
    z-index: 5;
}
#card-wrapper2 {
    z-index: 4;
}
#card-wrapper3 {
    z-index: 3;
}
#card-wrapper4 {
    z-index: 2;
}
#card-wrapper5 {
    z-index: 1;
}
#card-wrapper6 {
    z-index: 0;
}

#card0 {
    bottom: 60px;
    left: 30px;
}

#card1 {
    bottom: 50px;
    left: 25px;
}
#card2 {
    bottom: 40px;
    left: 20px;
}
#card3 {
    bottom: 30px;
    left: 15px;
}
#card4 {
    bottom: 20px;
    left: 10px;
}
#card5 {
    bottom: 10px;
    left: 5px;
}

.title-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-bottom: 0;
}

.symbol {
    color: white;
    font-size: 3rem;
    font-weight: lighter;
    height: 30px;
    line-height: 20px;
    margin: 0 auto;
    padding: 0;
    transform: rotate(-90deg);
    white-space: nowrap;
    width: 30px;
}

/* CONTACT */

/* #contact h2 {
    margin-bottom: 5%;
} */

#contact a {
    font-family: 'Inconsolata', monospace;
    font-size: 1.3rem;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media screen and (orientation: landscape) {
    #welcome {
        padding-top: 10%;
    }
    #contact {
        padding-top: 5%;
    }
}

@media screen and (orientation: landscape) and (max-height: 500px) {
    main {
        position: relative;
        top: 0;
    }
    #welcome {
        padding-top: 5%;
    }
    #portfolio {
        height: 100vh;
    }
    .portfolio-container {
        bottom: 0;
    }
    #contact {
        padding-bottom: 0;
        padding-top: 5%;
    }
}

@media screen and (min-width: 700px) {
    a {
        font-size: 1.1rem;
    }
    .emoji {
        font-size: 1.7rem;
    }
}
header {
    background-color: rgba(230, 152, 132, 0.4);
    display: flex;
    flex-direction: column;
    height: 120px;
    justify-content: space-between;
    padding: 4% 0 2% 0;
    width: 100%;
}

h1 {
    font-family: 'Inconsolata', monospace;
    font-size: 1.8rem;
    font-weight: 400;
    letter-spacing: .3em;
    padding: 0 15%;
    position: relative;
    text-align: center;
}

nav {
    width: 100%
}

.nav-links {
    display: flex;
    justify-content: space-around;
    list-style: none;
    margin: 0 auto;
    max-width: 330px;
    width: 80%;
}

li {
    display: inline;
}

@media screen and (orientation: landscape) and (max-height: 500px) {
    header {
        height: 100px;
    }
}

@media screen and (min-width: 700px) {
    h1 {
        font-size: 2rem;
    }
    .nav-links {
        display: flex;
        justify-content: flex-end;
        max-width: 100%;
        width: 100%;
    }

    nav li {
        margin-right: 5%;
    }
}

