#backgroundVideo {
    position: fixed;
    top: 0;
    right: -25%;
    width: 150%;
    z-index: -2;
}

#background {
    background-color: rgba(255, 255, 255, .3);
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
}

@media screen and (min-width: 500px) and (orientation: portrait) {
    #backgroundVideo {
        right: -20%;
        top: -15%;
        width: 125%;
    }
}

@media screen and (orientation: landscape) {
    #backgroundVideo {
        right: 0;
        top: -40%;
        width: 100%;
    }
}

@media screen and (orientation: landscape) and (min-width: 1200px) {
    #backgroundVideo {
        top: -60%;
    }
}
.card {
    background-color: rgb(255, 255, 255, 1);
    border: 1px solid var(--grey-purp);
    height: 275px;
    margin: 0 auto;
    padding: 3%;
    white-space: normal;
    width: 275px;
}

.transparent {
    height: 400px;
    width: 350px;
}

.cover {
    background-color: rgba(75, 75, 75, .7);
    border: 1px solid rgba(255, 255, 255, .8);
    height: 100%;
    padding-top: 20%;
    width: 100%;
}

.title-wrapper {
    background-color: rgba(75, 75, 75, .7);
    border: 1px solid rgba(255, 255, 255, .8);
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin: 0 auto;
    margin-bottom: 10px;
    padding: 5px 10px;
    text-align: center;
    width: 100%;
}

h3 {
    font-family: 'Inconsolata';
    font-size: 1.3rem;
    font-weight: 500;
}

.card-content {
    display: flex;
    flex-direction: column;
    height: 205px;
    justify-content: space-between;
    padding-bottom: 10px;
}

.description {
    background-color: transparent;
    font-family: 'Open Sans', sans-serif;
    font-size: .9rem;
    font-weight: 400;
    padding: 4px 5%;
    width: 100%;
}

.preview {
    background-color: rgba(75, 75, 75, .7);
    height: 40px;
    margin: 0 auto;
    width: 80px;
}

.preview p {
    color: white;
    font-family: 'Open Sans', sans-serif;
    font-size: .8rem;
    font-weight: 400;
    margin: 0 auto;
    padding: 0;
    text-align: center;
}

#triangle {
    bottom: 2px;
    padding-top: 6px;
}

.preview-text {
    bottom: 10px;
}

