.project-links {
    display: flex;
    justify-content: space-evenly;
}

.project-links a {
    border-bottom: 1px solid var(--grey-purp);
    font-size: .9rem;
    /* margin-top: 20px; */
}

.project-links a:hover {
    border-bottom: 1px solid rgb(142 159 145);
    color:rgb(142 159 145);
}

