footer {
    align-items: center;
    bottom: 0;
    display: flex;
    height: 40px;
    position: fixed;
    justify-content: space-around;
    width: 100%;
}

.icon {
    color: var(--grey-purp);
    font-size: 1.5rem;
}

.icon:hover {
    color: rgb(112, 99, 103);
}

@media screen and (orientation: landscape) and (max-height: 500px) {
    footer {
        position: relative;
    }
}

@media screen and (min-width: 700px) {
    .icon {
        font-size: 1.6rem;
    }
}
